/////////////////////////  C 



.datepicker-dropdown {
	padding: 30px;
	width: 350px;
	height: 370px;

	@media only screen and (max-width: 480px) {
		transform: scale(0.8) translateX(-64%);
		left: 50% !important;
		margin-top: -30px !important;
		padding: 16px;
	}

	&:before,
	&:after {
		display: none;
	}


	.datepicker-days {
		box-sizing: border-box;
	}

	.table-condensed {
		width: 100%;

		thead {
			tr:nth-child(3) {
				//transform: translateY(20px);
			}
		}

		.dow {
			width: 40px;
			height: 40px;
			color: $gray-dark;
			font-size: 14px;
		}

		.day {
			width: 38px;
			min-width: 38px;
			height: 38px;
			border-radius: 4px !important;
			padding: 0;
			font-weight: 500;
			font-size: 14px;
			line-height: 16px;
			color: $dark;

			&.active {
				background-color: $blue;
				background-image: none;

				color: #fff;

				&:hover {
					background-color: $blue;
				}
			}


			&.old, &.new {
				//opacity: 0;
				//pointer-events: none;
				color: $gray;
			}
		}
	}

	.datepicker-switch {
		font-weight: 500;
		font-size: 18px;
		line-height: 24px;

		&:hover {
			background-color: transparent !important;
		}
	}

	.prev,
	.next {
		max-width: 24px;
		max-height: 24px;
		width: 24px;
		height: 24px;
		padding: 0 !important;
		position: relative;
		box-sizing: border-box;

		&:hover {
			background-color: transparent !important;
		}

		&:before,
		&:after {
			content: '';
			position: absolute;
			display: block;
			width: 24px;
			height: 24px;
			background-color: #fff;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 1;
			background-position: center;
			background-repeat: no-repeat;
		}

		&:before {
			background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 8H15M1 8L8 1M1 8L8 15' stroke='%2386868B' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
		}

		&:after {
			opacity: 0;
			background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 8H15M1 8L8 1M1 8L8 15' stroke='%234BAAF3' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
		}

		&:hover {
			&:before {
				opacity: 0;
			}

			&:after {
				opacity: 1;
			}
		}
	}


	.next {

		&:before {
			background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 8H1M15 8L8 1M15 8L8 15' stroke='%2386868B' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
		}


		&:after {
			background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15 8H1M15 8L8 1M15 8L8 15' stroke='%234BAAF3' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
		}
	}

}
